import { useLocation, useNavigate, useSearchParams } from 'react-router-dom';
import { useState, useMemo } from 'react';
import { motion } from 'framer-motion'
import { routes } from '../App';
import { useBookingContext } from '../context/booking-context';
import Lib from '../lib/lib';
import BookingCalcLib from '../lib/booking-lib';
import validator from 'validator';
import withQuery from 'with-query';
import GLOBAL from '../utils/global';




export default function ReservationReview() {

    const location = useLocation();
    const navigate = useNavigate();
    const [acceptTerm, setAcceptTerm] = useState(true);
    const { booking, setBooking, bookingTax, bookingSession } = useBookingContext();
    const total = useMemo(() => {
        const rooms = Object.entries(booking.rooms);
        const nights = Lib.getNightsBetweenDates(booking.checkOutDate, booking.checkInDate);

        return rooms.map(r => r[1]).reduce((prev, curRoom) => {
            const planAmount = BookingCalcLib.calculateRoomPlan(curRoom, {
                adult: booking.adult,
                child: booking.child,
                coupon: booking.coupon,
                nights: nights
            });
            const count = curRoom.count;

            return {
                totalCount: prev.totalCount + count,
                adult: prev.adult + (count * curRoom.adult),
                child: prev.child + (count * curRoom.child),
                amountPerNight: prev.amountPerNight + (count * curRoom.amount),
                amount: prev.amount + (count * curRoom.amount * nights), // amount before discount
                totalDiscount: prev.totalDiscount + (count * planAmount.totalDiscount * nights), // total discount
                amountWithPlan: prev.amountWithPlan + (count * planAmount.amount * nights), // amount after plan 
                freeStay: Math.max(prev.freeStay, planAmount.freeStays)
            }
        }, {
            adult: 0,
            child: 0,
            amount: 0,
            totalCount: 0,
            amountWithPlan: 0,
            totalDiscount: 0,
            freeStay: 0,
            amountPerNight: 0
        })
    }, [booking]);
    const totalTax = BookingCalcLib.calculateTax({ taxs: bookingTax || [], amount: total.amount });



    const onConfirmed = async () => {

        const isValid = checkIsValidData();
        if(isValid){
            await fetch(withQuery(`${GLOBAL.SERVER_API}/api/v2/booking`), {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({
                    ...booking,
                    channelId: 2,
                    bookingSession: bookingSession.session,
                    rooms: Object.entries(booking.rooms).map(r => ({ id: r[1].id, count: r[1].count })),
                })
            }).then(res => {
                navigate(routes.ReservationConfirmed.path, { state: { booking } })
            }).catch(err => {
                console.error(err);
            })
        }else{

        }        
    }




    const checkIsValidData = () => {
        const isValidName = validator.isAscii(booking.name);
        const isValidEmail = validator.isMobilePhone(booking.phone);
        const isValidEtaDate = validator.isValidDate(booking.eta);
        const isValidCheckInDate = validator.isValidDate(booking.checkInDate);
        const isValidCheckOutDate = validator.isValidDate(booking.checkOutDate);
        return isValidName && isValidEmail && isValidCheckInDate && isValidCheckOutDate && isValidEtaDate;
    }




    return (
        <motion.div
            initial={{ translateX: '10rem', opacity: 0 }}
            animate={{ translateX: '0rem', opacity: 1 }}
            exit={{ translateX: '10rem', opacity: 0 }}
            transition={{ type: 'tween' }}
            key={routes.ReservationReview.path}
            className='flex flex-col justify-between h-full w-full'>
            <div className='flex flex-col p-3 gap-3 h-full overflow-y-auto'>
                <div className='flex flex-col gap-2'>
                    <div className="flex flex-col gap-1 w-full">
                        <label htmlFor="" className="font-bold opacity-60">Name <span className='font-light text-[0.8rem] text-red-500'>{'*'}</span></label>
                        <input placeholder="Name" type="text" className="p-2 rounded-xl w-full bg-gray-50" value={booking.name} onChange={({ target }) => setBooking(b => ({ ...b, name: target.value }))} />
                    </div>
                    <div className="flex flex-col gap-1 w-full">
                        <label htmlFor="" className="font-bold opacity-60">Phone <span className='font-light text-[0.8rem] text-red-500'>{'*'}</span></label>
                        <input placeholder="Phone" type="text" className="p-2 rounded-xl w-full bg-gray-50" value={booking.phone} onChange={({ target }) => setBooking(b => ({ ...b, phone: target.value }))} />
                    </div>
                    <div className="flex flex-col gap-1 w-full">
                        <label htmlFor="" className="font-bold opacity-60">Estimated time arrival <span className='font-light text-[0.8rem] text-red-500'>{'*'}</span></label>
                        <input placeholder="Email" type="date" max={booking.checkInDate} className="p-2 rounded-xl w-full bg-gray-50" value={booking.eta} onChange={({ target }) => setBooking(b => ({ ...b, eta: target.value }))} />
                    </div>
                    <div className="flex flex-col gap-1 w-full">
                        <label htmlFor="" className="font-bold opacity-60 flex items-center gap-1">Email <span className='font-light text-[0.75rem]'>{'(optional)'}</span></label>
                        <input placeholder="Email" type="text" className="p-2 rounded-xl w-full bg-gray-50" value={booking.email} onChange={({ target }) => setBooking(b => ({ ...b, email: target.value }))} />
                    </div>
                </div>
                <div className='flex flex-col gap-5'>
                    <div className='flex flex-col gap-1'>
                        <div className='font-bold text-[1.1rem]'>Reservation info</div>
                        <div className='flex flex-col gap-2'>
                            <div className='flex'>
                                <div className="flex flex-col w-full text-[0.9rem]">
                                    <div className="font-bold">Check in date</div>
                                    <div>{booking.checkInDate}</div>
                                </div>
                                <div className="flex flex-col w-full text-[0.9rem]">
                                    <div className="font-bold">Check in date</div>
                                    <div>{booking.checkOutDate}</div>
                                </div>
                            </div>
                            <div className='flex'>
                                <div className="flex flex-col w-full text-[0.9rem]">
                                    <div className="font-bold">Adult</div>
                                    <div>{booking.adult}</div>
                                </div>
                                <div className="flex flex-col w-full text-[0.9rem]">
                                    <div className="font-bold">Child</div>
                                    <div>{booking.child}</div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className='flex flex-col gap-1'>
                        <div className='font-bold text-[1.1rem]'>Rooms</div>
                        <div className='flex flex-col'>
                            {
                                Object.entries(booking.rooms).map((r, index) => {
                                    const room = r[1];
                                    return <div key={index}>{`${room.count}x ${room.name}`}</div>
                                })
                            }
                        </div>
                    </div>
                    <div className='flex flex-col'>
                        <div className='flex justify-between font-bold text-[1.1rem]'>
                            <div>Sub total</div>
                            <div>{`${total.amount.toLocaleString()} MMK`}</div>
                        </div>
                        <div className='flex justify-between'>
                            <div>Tax</div>
                            <div>{`${totalTax.total.toLocaleString()} MMK`}</div>
                        </div>
                        <div className='flex justify-between'>
                            <div>Discount</div>
                            <div>{`${total.totalDiscount.toLocaleString()} MMK`}</div>
                        </div>
                        <div className='py-2'>
                            <div className='text-[0.9rem]'>{'10 nights'}</div>
                            <div className='text-[1.2rem] font-bold'>{`${(total.amountWithPlan + totalTax.total).toLocaleString()} MMK`}</div>
                        </div>
                    </div>
                </div>
            </div>
            <div className='flex flex-col gap-3 p-3'>
                <div className='flex gap-2'>
                    <input type="checkbox" value={acceptTerm} onInput={({ target }) => setAcceptTerm((value) => {
                        const val = target.value == 'true';
                        return !val;
                    })} className='cursor-pointer' />
                    <label className='text-[0.9rem] underline cursor-pointer' onClick={() => navigate(routes.ReservationTermAndCondition.path)}>Accept term and condition</label>
                </div>
                <div className='flex gap-3'>
                    <button onClick={() => navigate(-1, { state: { booking } })} className=' w-full py-3 rounded-2xl shadow-2xl shadow-black/5 cursor-pointer bg-black text-white font-bold'>Back</button>
                    <button onClick={onConfirmed} disabled={acceptTerm} className='w-full py-3 rounded-2xl shadow-2xl shadow-black/5 cursor-pointer bg-primary disabled:bg-stone-200 transition-all duration-300 disabled:text-gray-400 font-bold'>Confirm</button>
                </div>
            </div>
        </motion.div>
    )
}